<template>
  <div class="app-container">
    <h1>What your Name?</h1>
    <el-input type="text" placeholder="请输入你的昵称" v-model="text"></el-input>
    <el-button @click="next">next</el-button>
  </div> 
</template>
<script>
import router from "../router/index"
export default {
  data(){
    return {
      text:""
    }
  },
  methods:{
    next(){
      window.localStorage.setItem("username",this.text)
      router.push("/rtc")
    }
  }
}
</script>
<style scoped>
.app-container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
</style>